// kode diatas dirubah menjadi arrow fanc menjadi :
export const fatchProducts = () => async (dispacth) => {
    const response = await fakeStoreApi.get("/products");
    dispacth({
      type : ActionType.FETCH_PRODUCTS,
      payload : response.data 
    })
}

export const setProducts = (products) => {
  return {
    type : ActionType.SET_PRODUCTS, // type ini akan dipanggil oleh reducer dengan membawa state(products) yg dikirim dari ProductListing.js
    payload : products,
    }
}






export const fatchAProduct = (id) => async (dispacth) => {
  const response = await fakeStoreApi.get(`/products/${id}`);
  dispacth({
    type : ActionType.SELECTED_PRODUCT,
    payload : response.data 
  })
}

export const selectedProduct = (product) => {
    return {
      type : ActionType.SELECTED_PRODUCT,
      payload : product,
      }
  }

export const removeSelectedProduct = (product) => {
    return {
      type : ActionType.REMOVE_SELECTED_PRODUCT,
      }
  }

